  background-color: #fff;
  justify-content: space-between;
  align-items: stretch;
  height: 60px;
  padding: 0 20px;
  transition: all .3s;
  display: flex;
  position: sticky;
  top: 0;
}

@media only screen and (width >= 700px) {
  header {
    padding: 0 60px;
  }
}

#menu {
  cursor: pointer;
  justify-content: space-evenly;
  align-items: center;
  gap: 20px;
  min-width: 80px;
  display: flex;
  position: relative;
}

@media only screen and (width >= 700px) {
  #menu {
    flex: 2;
    height: 100%;
  }
}

#lines > * {
  transition: all .3s;
}

#line1, #line3 {
  stroke-dasharray: 9;
}

#line2 {
  stroke-dasharray: 9;
  stroke-dashoffset: 5px;
}

.menu-active #line1, .menu-active #line3 {
  stroke-dashoffset: 5px;
}

.menu-active #line2 {
  stroke-dashoffset: 0;
}

#categories {
  color: #484848;
  opacity: .3;
  z-index: 5;
  background-color: #fff;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  transition: all .3s cubic-bezier(.03, .17, .47, 1.28);
  display: flex;
  position: absolute;
  top: 60px;
  left: -300px;
  transform: scale(.5);
  box-shadow: 1px 1px 4px gray;
}

@media only screen and (width >= 700px) {
  #categories {
    box-shadow: none;
    opacity: 1;
    z-index: 1;
    background-color: #0000;
    flex-direction: row;
    flex: 3;
    justify-content: center;
    align-items: center;
    height: 100%;
    position: static;
    top: 0;
    left: 0;
    transform: none;
  }
}

.menu-active + #categories {
  opacity: 1;
  transform: translateX(300px)scale(1);
}

@media only screen and (width >= 700px) {
  .menu-active + #categories {
    opacity: 1;
